import Vue from 'vue'
import VueI18n from 'vue-i18n' 
import zh from './zh.js'
import el from './el.js'

Vue.use(VueI18n)  

export var i18n = new VueI18n({
	locale: 'el_GR',
	messages: {
		"zh-CN": {
			...zh
		},
		"el_GR": {
			...el
		}
	}
})

// 这一行 一定要写 不然 app上 无法调用 国际化
Vue.prototype._i18n = i18n